.greeting {
    display: flex;
    align-items: flex-start;
}

.error {
    color: red;
    height: 21px;
    margin-left: 10px;
}
.input {
    margin: 10px;
    background: hotpink;
    color: gold;
}
.input:focus {
    outline: none;
    border: blue solid 2px;
}
.errorInput{
    margin: 10px;

    background: slategrey;
color: darkorchid;
    border: 2px solid red;
}
.button {
    margin: 10px;
    width: 60px;
    background: beige;
    color: black;
    outline: none;

}
.button:focus {
    outline: #99ff99 solid 1px;
}

.button:active {
    background: #99ff99;
}

.button:disabled {
    color: #005500;
}

.count {
    margin: 10px;
}